.commonHeader #headNav {
  height: 50px;
  background: #444;
  color: #eee;
  display: flex;
  justify-content: space-between;
}
.commonHeader #headNav .nav-logo-wrap {
  line-height: 50px;
  width: 50px;
  padding: 5px;
}
.commonHeader #headNav .nav-logo-wrap .ant-avatar-image {
  vertical-align: baseline;
  vertical-align: initial;
}
.commonHeader #headNav .nav-list-wrap {
  width: calc(100% - 50px);
  display: flex;
  flex-direction: row-reverse;
}
.commonHeader #headNav .nav-list-wrap .ant-menu {
  background: transparent;
  border: none;
}
.commonHeader #headNav .nav-list-wrap .ant-menu .ant-menu-item:not(.ant-menu-item-selected) > a {
  color: #eee;
}
.commonHeader #headNav .anticon {
  display: none;
  text-align: right;
  line-height: 50px;
}
.commonHeader > img {
  margin-top: 50px;
  width: 100%;
  max-height: 100px;
}
@media screen and (max-width: 576px) {
  .commonHeader #headNav .nav-list-wrap {
    display: none;
  }
  .commonHeader #headNav .anticon {
    display: inline-block;
  }
  .commonHeader > img {
    height: 50px;
  }
}
